// Libraries
import { Link } from "react-router-dom";
import styled from "styled-components";

// Import Resources
import {pageBgColor, fragBgColor, titleColor, lineColor} from "../../components/color/Color.js"

const media_max_width = "800px";
const nav_height = "38px";

// Hold Icon
export const IconContainer = styled(Link)`


`;


// Navigation Bar
export const NavBar = styled.div`

	align-item: center;	
	background-color: ${fragBgColor};
    border:1px solid ${lineColor};
	display: flex;
	height: ${nav_height};
	justift-content: space-around;
	line-height: ${nav_height};
	width: 100%;
	

	@media only screen and (max-width: ${media_max_width}) {
		flex-direction: column;
		float: none;
		height: auto;
		justify-content: center;
	}
`;


// Navigation Links
export const NavButton = styled(Link)`

	background-color: ${fragBgColor};
	color: ${titleColor};
	font-size: 13px;
    font-weight: bold;
    font-family: OpenSans, Helvetica, sans-serif;
	height: 100%;
	position: relative;
	padding: 0px 2px;
	text-decoration: none;
    text-align: center;
	width: 10%;


	&:hover {

		color: ${titleColor};
		text-decoration: underline;
	}

	@media only screen and (max-width: ${media_max_width}) {
		display:block;
        padding:0px;
        width:100%;
	}
`;

// Horizontal Line Encasing The Navigation Bar
export const NavLine = styled.hr`

	background-color: ${pageBgColor};
    border:none;height:4px;
    margin-top:0px;
    margin-bottom:0px;
`;

// Vertical Line Separating Each Navigation Link
export const NavDivider = styled.div`

	background-color:#EDF4F6;
    height:15px;
    margin: 7px 0px;
    width:2px;

    @media only screen and (max-width: ${media_max_width}) {
        display:none;
    }
`;

// Register/Login
export const RegLoginBtn = styled.button`

	background-color:transparent;
	border:1.5px solid ${fragBgColor};
    border-radius: 4px;
    float:right;
    margin:50px 30px 0 0;
    padding:10px;

    @media only screen and (max-width: ${media_max_width}) {
        
    }


`;