// React Libraries

import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";

// Import Pages
import Home from "../../pages/Home.js";
import Resume from "../../pages/Resume.js";

// Import Resources
// Sequence of the import in {} is very important
import {
  IconContainer,
  NavIcon,
  NavLine,
  NavBar,
  NavButton,
  NavDivider,
  RegLoginBtn,
} from "./NavigationElements";

import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import NavDropdown from "react-bootstrap/NavDropdown";

// Navigation Component
const Navigation = () => {
  return (
    <Router>
      <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
        <Navbar.Brand href={`${process.env.PUBLIC_URL}/`}>Tracie Ling</Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">
            <Link to={`${process.env.PUBLIC_URL}/resume`}>Resume</Link>
            <Nav.Link href="#pricing">Pricing</Nav.Link>
            <NavDropdown title="Dropdown" id="collasible-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">
                Another action
              </NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">
                Separated link
              </NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
      <Switch>
        <Route path={`${process.env.PUBLIC_URL}/`} exact component={Home} />
        <Route path={`${process.env.PUBLIC_URL}/resume`} component={Resume} />
      </Switch>
    </Router>

    //      <Router>
    //    <NavBar className="navbar">
    //         <NavButton id="active" to={`${process.env.PUBLIC_URL}/`}>
    //          Home
    //          </NavButton>
    //        <NavDivider className="divider" />
    //          <NavButton to={`${process.env.PUBLIC_URL}/resume`}>Resume</NavButton>
    //          <NavDivider className="divider" />
    //        </NavBar>
    //        <NavLine />
    //        <Switch>
    //          <Route path={`${process.env.PUBLIC_URL}/`} exact component={Home} />
    //          <Route path={`${process.env.PUBLIC_URL}/resume`} component={Resume} />
    //        </Switch>
    //      </Router>
  );
};

export default Navigation;
